import "./index.css";
import 'regenerator-runtime/runtime';

import { ABIs, Addresses } from "./contracts/contracts";
import React, { useEffect } from "react";

const ethers = require("ethers");

function MetasquatchDapp(props) {
  const subBtn = document.getElementsByClassName("submit-btn")
  const [amtToMint, setAmtToMint] = React.useState(1)

  let provider,
    address,
    signer,
    tokensMinted;
    const isMinting = React.createRef(false)

    const [mintContract, setMintContract ] = React.useState()
    const [tokenContract, setTokenContract ] = React.useState()

  const mint = React.useCallback(async () => {

    isMinting.current = true;
    let MINTING_COST = await mintContract.PRICE()
    //Mint Value in Ethereum
    const value = "0.1";
    //Amount of NFTs to Mint
    const amount = parseInt(amtToMint)
    var totalETHspent = MINTING_COST * amount;
    const formattedTotalETHSpent = ethers.utils.formatEther(totalETHspent.toString());

    const mintGas = await mintContract.estimateGas.purchase(amount, {
      value: (formattedTotalETHSpent * 10 ** 18).toString(),
    }).catch((err) => {
      console.error(err)
    })

    //Mint Function
   await  mintContract.purchase(amount, {
      value: (formattedTotalETHSpent * 10 ** 18).toString(),
      //gasLimit: mintGas
    })
      .catch((error) => {
        console.log(error.message);
        if (error.message.includes('insufficient')) alert(`You need more ETH to mint, make sure you have ${formattedTotalETHSpent} ETH available.`)
        isMinting.current = false;

      }).finally(() => {
        isMinting.current = false;  
      })

  }, [amtToMint])
  
  useEffect(() => {
    const func = async () => {

      //Get signer & provider
      provider = new ethers.providers.Web3Provider(window.ethereum);
      signer = provider.getSigner();

      //Contract Addresses
      const _tokenContract = new ethers.Contract(Addresses.tokenAddress, ABIs.tokenABI, signer);
      const _minisContract = new ethers.Contract(Addresses.minisAddress, ABIs.minisABI, signer);

      setMintContract(_minisContract);
      setTokenContract(_tokenContract);

      //Grab HTML Elements  
      const connectBtn = document.getElementById("connect");
      const mintBtn = document.getElementById("mint-btn")
      const button = document.getElementById("connect-text")


      //Connect to wallet
      const connect = async () => {
        //Require Chain
        window.ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: '0x1' }], // chainId must be in hexadecimal numbers
        });

        //Account Request
        window.ethereum.request({ method: 'eth_requestAccounts' })
          .then((response) => {
            console.log(response)
            window.localStorage.setItem('address', response[0]);
            button.innerHTML = "Connected"
          }).catch((error) => {
            alert('An error occurred, kindly check the console for details');
            console.log(error);
          })
        init()
      }

      //Initialize the app info
      const init = async () => {
        if (window.ethereum._state.isConnected) {
          console.log('Init')
          tokensMinted = await mintContract.totalSupply();

          const supply = document.getElementById("supply")
          supply.innerHTML = "Supply: " + tokensMinted + " / 2000"

          button.innerHTML = "Connected"


          address = window.localStorage.getItem('address')
        } else {
          button.innerHTML = "Connect"
        }
      }

      //When the site loads
      window.onload = () => {
        init()
      }

      //Mint


      if (!window.ethereum._state.isConnected) {
        window.localStorage.setItem('connected', 'Connected')
      } else {
        window.localStorage.setItem('connected', 'Connect')
      }

      connectBtn.addEventListener('click', (e) => {
        e.preventDefault();
        e.stopPropagation();
        connect();
      });

   

      window.ethereum.on('accountsChanged', (account) => {
        window.localStorage.setItem('address', account[0]);
        init()
        var button = document.getElementById("connect")
        console.log('Account changed!')
      });

      window.ethereum.on('chainChanged', () => {
        init()
        var button = document.getElementById("connect")
        console.log('Chain changed!')
      });

      window.ethereum.on('disconnect', () => {
        init()
        console.log('Account Disconnected!')
        button.innerHTML = "Connect"
      });
      return () => {
        console.log(`cleanup`)
      }
    }

    func();
  }, []);

  const validateAmountToMint = (newAmount) => {
    if (newAmount > 5) {
      alert("Cant mint more than 5 nfts at once!");
      newAmount = 5;
    } else if (newAmount < 1) {
      newAmount = 1;
    }
    setAmtToMint(newAmount)
  }

  const {
    logo
  } = props;

  return (
    <section className="section-gap mint">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <ul className="d-flex v-cntr mint-list">

              <li className="wallet">
                <div id="connect" title="Connect to Metamask" className="top-btn"><a id="connect-text" href="#">Connect</a></div>
              </li>
              <li className="wallet">
                <div className="top-btn"><a href="https://hukr.org" className="top-btn">View Website</a></div>
              </li>
            </ul>
          </div>
        </div>
        <div className="row mint-hdr-holder" style={{margin:0, padding:0}}>
          <div className="col-md-6">
            <p id="supply" className="mint-hdr">Supply: 0 / 2000</p>
          </div>
          <div className="col-md-6 ">
            <div className="list-group list-group-hover">
            <p className="m-0 list-group-item list-group-item-text mint-hdr text-end">Max Mint: 5</p>
            <p className="m-0 list-group-item list-group-item-text mint-hdr text-end">Mint Price: 3e9 HUKR Token</p>
            </div>
          </div>
        </div>
        <div className="row d-flex v-cntr mint-form">
          <div className="col-md-6 align-items-center d-flex">
            <div className="textarea-holder"><img className="img-fluid img-thumbnail img-nft" src={logo} alt="" /></div>
          </div>
          <div className="col-md-6 mint-form-holder">
            <div className="mint-form d-flex v-cntr">
              <div className="inp-group">
            
                <div className="inp-holder form-inline form-group ">
                <div className='badge badge-rounded bg-rounded bg-primary mb-2' htmlFor="tokenID">Amount</div>
                <input className='form-control' onChange={e => validateAmountToMint(+e.target.value)} id="tokenID" min="1" max="5" type="number" value={amtToMint} />
                </div>
              </div>
            </div>
            <div onClick={mint} id="mint-btn"><button className="submit-btn">Mint</button></div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default MetasquatchDapp;